@import url(https://fonts.googleapis.com/css?family=Overpass+Mono);
@import url(https://fonts.googleapis.com/css?family=Kanit:800);
body {
  background-color: black;
  color: white;
  font-size: 16px;
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 1rem;
  text-align: center;
}

.default-toast {
  background-color: #272121;
}

.default-toast button {
  border: none;
  background-color: transparent;
  color: #ee283b;
  font-family: 'Kanit', sans-serif;
  font-weight: 800;
  font-size: 1rem;
  letter-spacing: 1.5px;
  padding: 0.5rem;
}
.search-view {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.search-logo {
    margin-bottom: 3rem;
    margin-top: 1rem;
}

.lbl-busca {
    font-family: 'Kanit', sans-serif;
    font-weight: 800;
    font-size: 1.5rem;
    letter-spacing: 1.5px;
}

.lbl-info {
    font-family: 'Overpass Mono', monospace;
    font-size: 0.6rem;
}

.btn-lista {
    margin-top: 0.5rem;
}
.button {
    border: none;
    border-radius: 4px;
    background-color: #272121;
    color: #ee283b;
    font-family: 'Kanit', sans-serif;
    font-weight: 800;
    font-size: 1rem;
    letter-spacing: 1.5px;
    padding: 0.5rem;
}
.search-group {
    margin: 0.5rem 0 0.5rem 0;
    display: -webkit-flex;
    display: flex;
}

.search-group input {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    border: none;   
    border-radius: 0px;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    height: 1.5rem;
    padding: 0.5rem;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
}

.search-group button {
    background-color: #4fc725;
    border: none;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    height: 2.5rem;
    padding: 2px 0 0 0;
    width: 2.5rem;
}
.logo {
    -webkit-align-self: center;
            align-self: center;
    background-color: black;
}
.result-view {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.lbl-result-header {
    font-family: 'Kanit', sans-serif;
    font-weight: 800;
    font-size: 0.75rem;
    letter-spacing: 1.5px;
    margin-bottom: -5px;
}

.lbl-result-search {
    font-family: 'Kanit', sans-serif;
    font-weight: 800;
    font-size: 1.5rem;
    letter-spacing: 1.5px;
}

.result-table {
    margin: 1rem 0 1rem 0;
    text-align: left;
}

.result-table .no-rows {
    padding: 0.25rem 0.5rem
}

.result-table thead, .result-table .ReactVirtualized__Table__headerRow {
    color: #ee283b;
    font-family: 'Kanit', sans-serif;
    font-weight: 800;
    font-size: 0.75rem;
    letter-spacing: 1.5px;
}

.result-table tbody, .result-table .ReactVirtualized__Table__Grid {
    background-color: white;
    color: black;
    font-family: 'Overpass Mono', monospace;
    font-size: 0.8rem;
}
.result-table td, .result-table th {
    padding: 0.25rem 0.5rem
}

.result-table .ReactVirtualized__Table__rowColumn {
    white-space: normal;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
}

.result-table .ReactVirtualized__Table__rowColumn:first-child {
    margin-right: 0px;
}


.result-table .ReactVirtualized__Table__rowColumn:not(:first-child) {
    border-left: 1px solid black;
    padding-left: 10px;
}


.result-table tr td:not(:last-child) {
    border-right: 1px solid black
}

.result-table th, .result-table tr:not(:last-child), .result-table .ReactVirtualized__Table__row:not(:last-child) {
    border-bottom: 1px solid black
}

.result-logo {
    margin-bottom: 1rem;
    margin-top: 8rem;
}
.floating-back-button {
    bottom: -50px;
    left: 25px;
    position: fixed;
    -webkit-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
}

.floating-back-button.show {
    bottom: 10px;
}
